.ten-day-container{
  background: #f7edff;
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  justify-items: center;
  -ms-flex-align: center;
      align-items: center;
  grid-row-gap: 40px;
  padding: 2%;
  border-radius: 10px;
  -webkit-box-shadow:  0px 15px 5px rgba(0,0,0,0.1),
  10px 20px 5px rgba(0,0,0,0.05),
  -10px 20px 5px rgba(0,0,0,0.05);
          box-shadow:  0px 15px 5px rgba(0,0,0,0.1),
  10px 20px 5px rgba(0,0,0,0.05),
  -10px 20px 5px rgba(0,0,0,0.05);
  margin-bottom: 2%;
  font-family: 'IBM Plex Sans', sans-serif;
}

.ten-day {
  display: grid;
  justify-items: center;
  align-self: center;
}


