@import url('https://fonts.googleapis.com/css?family=IBM+Plex+Sans:700');

*{
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

.weather {
  background: #F5EFFF;
  /* width: 50%; */
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  padding: 2%;
  border-radius: 10px;
  justify-items: center;
  -ms-flex-align: center;
      align-items: center;
  -webkit-box-shadow:  0px 15px 5px rgba(0,0,0,0.1),
  10px 20px 5px rgba(0,0,0,0.05),
  -10px 20px 5px rgba(0,0,0,0.05);
          box-shadow:  0px 15px 5px rgba(0,0,0,0.1),
  10px 20px 5px rgba(0,0,0,0.05),
  -10px 20px 5px rgba(0,0,0,0.05);
}

.weather__city {
  font-family: 'Elsie', cursive;
  justify-self: center;
  grid-column: span 3;
}

.weather__fullForecast {
  padding: 13px;
  grid-column: 1/ span 3;
}

.weather__icon {
  width: 100px;
}

.weather__temp {
  font-family: 'IBM Plex Sans', sans-serif;
  text-align: center;
}

.weather__subtitle {
  text-align: center;
  font-family: 'IBM Plex Sans', sans-serif;
}

.weather__todayHigh {
  font-size: 3em;
  font-family: 'IBM Plex Sans', sans-serif;
}

.weather__todayLow {
  font-size: 3em;
  font-family: 'IBM Plex Sans', sans-serif;
}


