
/* font-family: 'Elsie', cursive;
font-family: 'Open Sans', sans-serif; */
.app {
  display: grid;
  grid-gap: 25px;
  padding: 25px;
  font-family: 'Open Sans', sans-serif;
}

.app__header {
  -ms-flex-item-align: center;
      align-self: center;
  justify-self: center;
  border-bottom: 2px solid black;
  text-shadow:  0px 15px 5px rgba(0,0,0,0.1),
  10px 20px 5px rgba(0,0,0,0.05),
  -10px 20px 5px rgba(0,0,0,0.05);
}

.app__title {
  font-family: 'Elsie', cursive;
  font-size: 5rem;
  margin: 0;  
}
